export default {
    
    getModalActionAndData: state =>  state,

    getModalTitle: state => {
        switch (state.heading) {
            case 'report_title':
                return 'रिपोर्ट की समस्या';
            case 'edit_pratilipi_summary':
            	return 'रचना  का सारांश';
            case 'edit_pratilipi_title':
            	return 'शीर्षक';
            case 'pratilipi_delete_content':
                return 'हटाएँ';
        }
    },

    getInputModalSubheadings: state => {
    	switch (state.heading) {
            case 'report_title':
                return ['रिपोर्ट की समस्या'];
            case 'edit_pratilipi_summary':
            	return ['संक्षिप्त सारांश दर्ज करें'];
            case 'edit_pratilipi_title':
            	return ['यहाँ शीर्षक लिखें', 'शीर्षक को दुबारा अंग्रेजी में लिखें'];
            default:
            	return []
        }	
    },

    getModalMessage: state => {
        switch (state.message) {
            case 'pratilipi_confirm_delete_content':
                return 'साहित्य हटाने की पुष्टि करें';
        }
    }
}



